import $ from 'jquery';

$(document).ready(() => {
  let expanded = false;

  function showCheckboxes() {
    const checkboxes = document.querySelector('.expandable-checkbox-list__menu');
    if (!expanded) {
      checkboxes.style.display = 'block';
      expanded = true;
    } else {
      checkboxes.style.display = 'none';
      expanded = false;
    }
  }
});
